<template>
<div class="Product-list">
  <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Product_number")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="5">
        <p>{{$t("common.Company_name")}}&nbsp</p>
        <Input placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="10">
        <p>{{$t("common.Submission_time")}}&nbsp</p>
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker>
        <span>-</span>
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
  </Row>
  <Table highlight-row :columns="columns1" :data="historyData" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  <Row type="flex" justify="center" class="code-row-bg page-data">
    <Col span="24">
      <Page :total="dataCount" :page-size="pageSize" @on-change="changepage"></Page>
    </Col>
  </Row>
</div>
</template>

<script>
export default {
    name:'Product_list',
    data () {
        return {
            ajaxHistoryData:[],
              // 初始化信息总条数
            dataCount:0,
              // 每页显示多少条
            pageSize:10,
            historyData: [],
            columns1: [
                {
                    title: this.$t("common.Product_number"),
                    key: 'product_number',
                    align:'center'
                },
                {
                    title: this.$t("common.Brand"),
                    key: 'brand',
                    align:'center',
                    render:(h,params) =>{
                      return h('img',{
                        attrs: {
                          src: params.row.brand
                        },
                        props: {

                        },
                        style: {
                          width:'100px'
                        }
                      },'')
                    }
                },
                {
                    title: this.$t("common.Product_name"),
                    key: 'product_name',
                    align:'center'
                },
                {
                    title: this.$t("common.Product_type"),
                    key: 'product_type',
                    align:'center',
                },
                {
                    title: this.$t("common.Company_name"),
                    key: 'corporate_name',
                    align:'center'
                },
                {
                    title: this.$t("common.Submission_time"),
                    key: 'time',
                    align:'center'
                },
                {
                    title: this.$t("common.Operate"),
                    key: 'operation',
                    align:'center',
                    render: (h, params) => {
                      return h('div', [
                          h('Button', {
                              props: {
                                  type: 'primary',
                                  size: 'small'
                              },
                              style: {
                                  marginRight: '10px'
                              },
                              on: {
                                  click: () => {
                                      //this.show(params.index)
                                  }
                              }
                          }, this.$t("common.Adopt")),
                          h('Button', {
                              props: {
                                  type: 'error',
                                  size: 'small'
                              },
                              on: {
                                  click: () => {
                                      //this.remove(params.index)
                                  }
                              }
                          }, this.$t("common.Overrule"))
                      ]);
                  }
                }
            ],
            data1: this.mockTableData1 (),
            Personal_info:null,
            clickData:null
        }
    },
    methods:{
      handleListApproveHistory(){
          // 保存取到的所有数据
          this.ajaxHistoryData = this.data1
          this.dataCount = this.data1.length;
          // 初始化显示，小于每页显示条数，全显，大于每页显示条数，取前每页条数显示
          if(this.data1.length < this.pageSize){
              this.historyData = this.ajaxHistoryData;
          }else{
              this.historyData = this.ajaxHistoryData.slice(0,this.pageSize);
          }
      },
      changepage(index){
          var _start = ( index - 1 ) * this.pageSize;
          var _end = index * this.pageSize;
          this.historyData = this.ajaxHistoryData.slice(_start,_end);
      },
      mockTableData1 () {
          let data = [];
          for (let i = 1; i < 12; i++) {
              data.push({
                  SID:  i,
                  brand: '../static/img/u'+i+'.png',
                  corporate_name: '深圳怡亚通股份有限公司',
                  product_name:'深圳怡亚通',
                  product_type:'借贷',
                  product_number:'DG620171016000'+i,
                  time:this.formatDate(this.getRandomDateBetween())
              })
          }
          return data;
      },
      getRandomDateBetween() { // 要求a, b 都是Date类型, 且a < b
          let date = new Date(),
          a = + new Date("October 10, 2017 11:13:00"),
          b = + new Date();
          date.setTime(Math.random()*(b - a) + a);
          return date;
      },
      formatDate (date) {
          const y = date.getFullYear();
          let m = date.getMonth() + 1;
          m = m < 10 ? '0' + m : m;
          let d = date.getDate();
          d = d < 10 ? ('0' + d) : d;
          let h = date.getHours();
          h = h < 10 ? ('0' + h) : h;
          let M = date.getMinutes();
          M = M < 10 ? ('0' + M) : M;
          let s = date.getSeconds();
          s = s < 10 ? ('0' + s) : s;
          return y + '-' + m + '-' + d + ' ' + h + ':' + M + ':' + s;
      },
      getMoble() {
          var prefixArray = new Array("130", "131", "132", "133", "135", "137", "138", "170", "187", "189");
          var i = parseInt(10 * Math.random());
          var prefix = prefixArray[i];

          for (var j = 0; j < 8; j++) {
              prefix = prefix + Math.floor(Math.random() * 10);
          }
          return prefix
      },
      onedata (data,index){
        console.log(data,index)
        this.Personal_info = 'examine'
        this.clickData = data
        this.sendParams ()
      },
      sendParams () {
          this.$router.push({
              path: '/Product_detail',
              name: 'Product_detail',
              params: {
                  name: 'Personal-info',
                  send_data: this.Personal_info,
                  click_data: this.clickData
              }
              /*query: {
                name: 'Personal-info',
                send_data: this.Personal_info
              }*/
          })
       }
    },
    created(){
         this.handleListApproveHistory();
    }
}
</script>

<style lang="css">
.Product-list{
  
}
.Product-list .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
.Product-list .search_criteria p{
  height: 30px;
}
.Product-list .page-data{
  height: 50px;
  line-height: 50px;
  border: 1px solid #DDDEE1;
  border-top: none;
  background-color: #fff;
  text-align: center;
}
</style>



